//== Vanilla Bootstrap
@import "node_modules/bootstrap/less/bootstrap.less";

//== TQ overrides
@import "node_modules/tafe-queensland-development-starter-kit-legacy/less/main.less";

//== Helper classes
@import "less/helpers.less";

//== Helper classes
@import "less/mtg-panels.less";

//== Local overrides

//Matching container to full width of d2l content frame
@container-desktop:         ((910px + @grid-gutter-width));

a i.tq-extlink { 
    padding-right: 2px;
    overflow: hidden;
    display: inline-block; 
    width:1em; 
    height:1em; 
    margin-left: 0.25em;
}
blockquote a i.tq-extlink { 
    height:1.3em;
}
blockquote a > i.tqi-extlink:before {
    content: "\e600";
}
