//== Typographical styles

h4:not([class]) {
  font-weight: 700;
}

p > a:not(.btn),
ul:not(.nav) li > a,
p > b > a,
table a:not(.btn),
blockquote > footer a {
  text-decoration: @link-hover-decoration;
}

p > a:not(.btn):hover,
ul:not(.nav) li > a:hover,
p > b > a:hover,
table a:not(.btn):hover,
blockquote > footer a:hover {
  text-decoration: none;
}

// Blockquotes
blockquote {
  footer,
  small,
  .small {
    font-size: @font-size-base; // back to default font-size
  }
}
