//== Educational Panels with icons

.tq-panel h3.panel-title {
  margin-top: 7px;
}

.tq-panel .panel-heading {
  padding-left: 10px;
}

.tq-panel-icon {
  margin-right: 10px;
}

/* Eliminating last-child bottom margin for a more balanced 
typographical look */
.panel-body p:last-child {
    margin-bottom: 0px;
}

//== Collapse (accordion) Panels
/* Accordion chevron indicator */
.panel-group[role=tablist] .panel-heading a:after {
    /* symbol for "opening" panels */
    font-family: 'Glyphicons Halflings';  /* essential for enabling glyphicon */
    content: "\e114";    /* adjust as needed, taken from bootstrap.css */
    float: right;        /* adjust as needed */
    color: @gray;         /* adjust as needed */
}

.panel-group[role=tablist] > 
  .panel-default >
    .panel-heading:hover {
      // cursor: pointer; 
      background-color: @gray-lighter;
  }

.panel-group[role=tablist] .panel-heading a.collapsed:after {
    /* symbol for "collapsed" panels */
    content: "\e080";    /* adjust as needed, taken from bootstrap.css */
}