/*Margin resets*/

.no-margin-left {
  margin-left: 0;
}

.no-margin-top {
  margin-top: 0;
}

.no-margin-right {
  margin-right: 0;
}

.no-margin-bottom {
  margin-bottom: 0;
}

/*Padding resets*/

.no-padding-left {
  padding-left: 0;
}

.no-padding-top {
  padding-top: 0;
}

.no-padding-right {
  padding-right: 0;
}

.no-padding-bottom {
  padding-bottom: 0;
}

/* Line height based paddings */
.tq-padding-lhc {
  padding: @line-height-computed;
}

.tq-padding-lhc-half {
  padding: (@line-height-computed / 2);
}

/* Gutter based paddings */

.tq-padding-gutter {
  padding: @grid-gutter-width;
}

.tq-padding-gutter-half {
  padding: (@grid-gutter-width / 2);
}

/* Line height based margins */
.tq-top-margin-lhc {
  margin-top: @line-height-computed;
}

.tq-bottom-margin-lhc {
  margin-bottom: @line-height-computed;
}

.tq-top-margin-lhc-half {
  margin-top: (@line-height-computed / 2);
}

.tq-bottom-margin-lhc-half {
  margin-bottom: (@line-height-computed / 2);
}

/* Gutter based margins */

.tq-top-margin-gutter {
  margin-top: @grid-gutter-width;
}

.tq-bottom-margin-gutter {
  margin-bottom: @grid-gutter-width;
}

.tq-top-margin-gutter-half {
  margin-top: (@grid-gutter-width / 2);
}

.tq-bottom-margin-gutter-half {
  margin-bottom: (@grid-gutter-width / 2);
}
