/*!
	*TQ Icon Implementation
	*version 2.1
*/
@font-face {
	font-family: 'TQI';
	src: url('../fonts/TQ_IconSet.eot');
	src: url('../fonts/TQ_IconSet.eot?#iefix') format('embedded-opentype'), url('../fonts/TQ_IconSet.woff') format('woff'), url('../fonts/TQ_IconSet.ttf') format('truetype'), url('../fonts/TQ_IconSet.svg?#TQ_IconSet') format('svg');
	font-weight: normal;
	font-style: normal
}

.tqi {
	display: inline-block;
	font: normal normal normal 14px/1 TQI, "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	word-spacing: -0.25em;
}
.tqi-panel {
	margin-top: 5px; margin-bottom: 11px;	
	display: block;
}
.tqi-lg {
	font-size: 1.33333333em;
	line-height: .75em;
	vertical-align: -15%
}
.tqi-2x {
	font-size: 2em
}
.tqi-3x {
	font-size: 3em
}
.tqi-4x {
	font-size: 4em
}
.tqi-5x {
	font-size: 5em
}
.tqi-fw {
	width: 1.28571429em;
	text-align: center
}
.tqi-border {
	padding: .2em;
	border: solid .08em #eee;
	border-radius: .2em
}
.tqi-pull-left {
	margin-right: .3em;
	float: left;
}
.tqi-pull-right {
	margin-left: .3em;
	float: right;
}
.tqi-invert {
	color: white;	
}

.tqi-stack {
	position: relative;
	display: inline-block;
	width: 2em;
	height: 2em;
	line-height: 2em;
	vertical-align: middle
}
.tqi-stack-1x, .tqi-stack-2x, .tqi-stack-3x {
	position: absolute;
	left: 0;
	width: 100%;
	text-align: center
}
.tqi-stack-1x {
	line-height: inherit
}
.tqi-stack-2x {
	font-size: 2em
}
.tqi-stack-3x {
	font-size: 3em
}

