/*! tq-gc helper classes */

.top-buffer {
  	margin-top: 15px;
}

.bottom-buffer {
	margin-bottom: 15px;	
}

.left-buffer {
    margin-left: 15px;
}

.right-buffer {
    margin-right: 15px;
}

.row-no-gutter {
	.make-row(@gutter: 0px)
}

.notransition {
	-webkit-transition: none !important;
	-moz-transition: none !important;
	-o-transition: none !important;
	-ms-transition: none !important;
	transition: none !important;
}

.thumbnail.with-caption.borderless {
    border: none;
    padding: 0px;
}
.thumbnail.with-caption {
    .word-wrap;
}
.thumbnail.with-caption p {
    margin: 0;
    padding-top: 0.5em;
}
.thumbnail.with-caption small:before {
    content: '\2014 \00A0';
}
.thumbnail.with-caption small {
    width: 100%;
    text-align: right;
    display: inline-block;
    color: #999;
}

.word-wrap {
    word-break: break-word;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
}

.longtextwrap {
    /* These are technically the same, but use both */
    overflow-wrap: break-word !important;
    word-wrap: break-word !important;
    -ms-word-break: break-all !important;
    /* This is the dangerous one in WebKit, as it breaks things wherever */
    word-break: break-all !important;
    /* Instead use this non-standard one: */
    word-break: break-word !important;
}

/* Tweaks */
/* Cursor pointer for accordion panels */
div.panel-group[role=tablist] > div.panel > div.panel-heading {
    cursor: pointer;
}

/* First paragraph top margin fix - for pages that do not start with h2 */
div.container > div.row > div.col-md-12 > p:first-of-type {
    margin-top: ((@line-height-computed / 2));
}

div.container > div.row > div.col-md-12 > div.panel:first-child {
    margin-top: (@grid-gutter-width / 2);
}

/*! end of tq-gc helper classes */